<template>
  <fixed-layout>
    <template slot="header">
      <mt-header title="俱乐部认证">
        <router-link to="/" slot="left">
          <mt-button icon="back" @click="$router.go(-1)"></mt-button>
        </router-link>
      </mt-header>
    </template>
    <mt-cell
      title="选择俱乐部类型"
      :value="clubType.label"
      is-link
      @click.native="sheetClubType.visible=true">
    </mt-cell>
    <mt-actionsheet
      cancelText=""
      :actions="sheetClubType.actions"
      v-model="sheetClubType.visible">
    </mt-actionsheet>
    <mt-field label="俱乐部名称" placeholder="请输入俱乐部名称" class="right" v-model="name"></mt-field>
    <mt-field label="俱乐部所在城市" placeholder="请输入俱乐部所在城市" class="right" v-model="city"></mt-field>
    <mt-field type="tel" label="俱乐部联系方式" placeholder="请填写正确的手机号码" class="right" v-model="phone"></mt-field>
    <mt-field type="mail" label="邮箱" placeholder="请填写邮箱" class="right" v-model="email"></mt-field>
    <mt-field label="微信号" placeholder="请填写微信号" class="right" v-model="wxId"></mt-field>
    <mt-field type="number" label="银行账号" placeholder="请填写银行账号" class="right" v-model="bankNo"></mt-field>
    <mt-cell title="俱乐部头像"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="headimg.url"
            @load="(e,file)=>handLoadImg(e,file,'headimg')"
            @delete="handDeleteImage(headimg)">
          </file-image2>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部主页图" value="请上传横图"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="mainimg.url"
            @load="(e,file)=>handLoadImg(e,file,'mainimg')"
            @delete="handDeleteImage(mainimg)">
          </file-image2>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部简介"></mt-cell>
    <mt-field type="textarea" placeholder="请填写俱乐部简介" v-model="desc"></mt-field>
    <mt-cell title="上传认证材料" value="身份证正反面和营业执照"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="idcardF.url"
            @load="(e,file)=>handLoadImg(e,file,'idcardF')"
            @delete="handDeleteImage(idcardF)">
          </file-image2>
          <h4>身份证正面</h4>
        </li>
        <li>
          <file-image2
            :src="idcardB.url"
            @load="(e,file)=>handLoadImg(e,file,'idcardB')"
            @delete="handDeleteImage(idcardB)">
          </file-image2>
          <h4>身份证反面</h4>
        </li>
        <li>
          <file-image2
            :src="licenceimg.url"
            @load="(e,file)=>handLoadImg(e,file,'licenceimg')"
            @delete="handDeleteImage(licenceimg)">
          </file-image2>
          <h4>营业执照</h4>
        </li>
      </ul>
    </section>
    <cropper-box
      v-show="cropper.dialog"
      class="cropper-box"
      :src="cropper.src"
      :ar="cropper.size.width/cropper.size.height"
      @cancel="cropper.dialog = false"
      @done="handCropperDone">
    </cropper-box>
    <section class="ft">
      <p class="agreement"><input type="checkbox" v-model="agree"/><a href="">同意《号角平台商户服务协议》</a></p>
      <mt-button :disabled="!agree" type="primary" @click="handSubmit">确认</mt-button>
    </section>
  </fixed-layout>
</template>

<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import FileImage2 from '@/components/file-image2.vue'
  import CropperBox from '@/components/cropper-box.vue'

  export default {
    name: 'club',
    components: {
      FixedLayout,
      FileImage2,
      CropperBox,
    },
    data() {
      return {
        cropper: {
          dialog: false,
          src: '',
          item: null,
          size: {
            width: 1000,
            height: 1000,
          },
        },
        agree: false,
        clubType: {
          value: -1,
          label: '请选择'
        },
        sheetClubType: {
          visible: false,
          actions: []
        },
        name: '',
        city: '',
        phone: null,
        email: '',
        wxId: '',
        bankNo: null,
        desc: '',
        headimg: {
          file: null,

          url: ''
        },
        mainimg: {
          file: null,

          url: ''
        },
        idcardF: {
          file: null,

          url: ''
        },
        idcardB: {
          file: null,

          url: ''
        },
        licenceimg: {
          file: null,

          url: ''
        },
      }
    },
    computed: {
      ...mapState({
        ClubType: state => state.config.ClubType,
      })
    },
    watch: {
      ClubType: {
        immediate: true,
        handler(value) {
          if (value instanceof Array) {
            value.forEach(e => {
              this.sheetClubType.actions.push({
                name: e.label,
                method: () => {
                  this.clubType = e
                }
              })
            })
          }
        }
      },
    },
    created() {
      new Promise((resolve, reject) => {
        if (this.$route.query.clubId) {
          this.$http.get(this.$port.MY_CLUB, {
            params: {
              clubId: this.$route.query.clubId
            }
          }).then(res => {
            res.data = res.data || {}
            if (res.code === 200) {
              this.clubType.value = res.data.clubType
              this.name = res.data.name
              this.city = res.data.city
              this.phone = res.data.phone
              this.email = res.data.email
              this.wxId = res.data.wxId
              this.bankNo = res.data.bankNo
              this.desc = res.data.desc
              this.headimg.url = res.data.headimg
              this.mainimg.url = res.data.mainimg
              this.idcardF.url = res.data.idcardF
              this.idcardB.url = res.data.idcardB
              this.licenceimg.url = res.data.licenceimg
            }
            resolve()
          })
        } else {
          let name = 'club_'
          this.name = localStorage.getItem(`${name}name`)
          this.city = localStorage.getItem(`${name}city`)
          this.phone = localStorage.getItem(`${name}phone`)
          this.email = localStorage.getItem(`${name}email`)
          this.wxId = localStorage.getItem(`${name}wxId`)
          this.bankNo = localStorage.getItem(`${name}bankNo`)
          this.desc = localStorage.getItem(`${name}desc`)
          resolve()
        }
      }).then(() => {
        let name = 'club_'
        setInterval(() => {
          localStorage.setItem(`${name}name`, this.name || '')
          localStorage.setItem(`${name}city`, this.city || '')
          localStorage.setItem(`${name}phone`, this.phone || '')
          localStorage.setItem(`${name}email`, this.email || '')
          localStorage.setItem(`${name}wxId`, this.wxId || '')
          localStorage.setItem(`${name}bankNo`, this.bankNo || '')
          localStorage.setItem(`${name}desc`, this.desc || '')
        }, 200)
      })
    },
    methods: {
      handDeleteImage(obj) {
        obj.file = null
        obj.url = ''
      },
      handLoadImg(e, file, prop) {
        this.cropper.item = this[prop]
        this.cropper.file = file
        this.$commons.compressorImage(file).then(res => {
          if (prop === 'headimg' ||
            prop === 'mainimg') {
            this.cropper.dialog = true
            if (prop === 'headimg') {
              this.cropper.size.width = 200
              this.cropper.size.height = 200
            } else if (prop === 'mainimg') {
              this.cropper.size.width = 750
              this.cropper.size.height = 750 / 1.5
            }
            setTimeout(() => {
              this.cropper.src = window.URL.createObjectURL(res)
            }, 200)
          } else {
            this.cropper.size.width = 1000
            this.cropper.size.height = 1000
            this.$commons.uploadImage(this.cropper.file).then(res => {
              if (res.code === 200) {
                this.cropper.item.url = res.data
              }
            })
          }
        })
      },
      handCropperDone(cropper) {
        cropper.getCroppedCanvas({
          width: this.cropper.size.width,
          height: this.cropper.size.height,
        }).toBlob(blob => {
          this.$commons.uploadImage(blob).then(res => {
            if (res.code === 200) {
              this.cropper.item.url = res.data
              this.cropper.dialog = false
            }
          })
        })
      },
      handSubmit() {
        if (!this.agree) {
          alert('请阅读并同意协议条款')
          return
        }
        let params = {
          clubId: 0,
          clubType: this.clubType.value,
          name: this.name,
          city: this.city,
          phone: this.phone,
          email: this.email,
          wxId: this.wxId,
          bankNo: this.bankNo,
          headimg: this.headimg.url,
          mainimg: this.mainimg.url,
          idcardF: this.idcardF.url,
          idcardB: this.idcardB.url,
          licenceimg: this.licenceimg.url,
          desc: this.desc
        }
        this.$http.post(this.$port.MY_CLUB, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {
            alert('资料提交成功')
            this.$router.push('/')
          } else {
            alert(res.msg)
          }
        })
      }
    }
  }
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
  .m-uploads {
    background-color: #fff;
    padding: 0 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        flex: 0 0 33%;
        b {
          background-color: rgba(0, 0, 0, 0.5);
          text-align: center;
          font-weight: 400;
          font-size: 8px;
          color: #fff;
          width: 20px;
          height: 20px;
          position: absolute;
          right: 0;
          top: 0;
        }
        h4 {
          font-size: 12px;
          text-align: center;
          font-weight: 400;
          color: #333;
          padding-top: 5px;
        }
      }
    }
    .ui-upload {
      width: 110px;
      height: 110px;
    }
  }

  .uploadImage {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
  }

  .ft {
    padding: 15px 10px;
    border-top: solid #e5e5e5 1px;
    .agreement {
      font-size: 12px;
      margin-bottom: 10px;
      input[type='checkbox'] {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .mint-button {
      width: 100%;
    }
  }
</style>



